/* TABLA ====================================================== */
table {
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
  border-spacing: 1px;
  text-align: start;
  border-collapse: collapse;
}

thead {
  min-height: 56px;
  border-bottom: 1px solid var(--md-sys-color-outline);
}
.table-sm thead {
  min-height: 36px;
}

tr {
  min-height: 52px;
  border-bottom: 1px solid var(--md-sys-color-outline);
  overflow: hidden;
}
.table-sm tr {
  min-height: 36px;
}

tr:first-child {
  border-top: none;
}

tr:last-child {
  border-bottom: none;
}

td {
  padding: 6px 16px;
  color: var(--md-sys-color-on-surface);
  margin: 0;
  overflow: hidden;
}
td.tcenter {
  text-align: center;
}

th {
  padding: 6px 16px;
  height: 56px;
  color: var(--md-sys-color-on-surface);
  text-align: start;
  overflow: hidden;
  font-weight: 600;
}
.table-sm th {
  height: 36px;
