  cursor: pointer;
}
.textfield-filled .btn:focus-visible, .textfield-filled .btn:focus {
  outline: none;
}
.textfield-filled .btn::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  background-color: var(--md-sys-color-on-surface);
  transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
  border-radius: 50%;
  width: 0;
  height: 0;
  opacity: 0;
}
.textfield-filled .btn:hover::before, .textfield-filled .btn:focus::before, .textfield-filled .btn:active::before {
  top: calc(50% - 18px);
  left: calc(50% - 18px);
  width: 36px;
  height: 36px;
}
.textfield-filled .btn:hover::before {
  opacity: 0.08;
}
.textfield-filled .btn:focus::before {
  opacity: 0.12;
}
.textfield-filled .btn:active::before {
  opacity: 0.12;
}
.textfield-filled label {
  margin: 16px;
  color: var(--md-sys-color-on-surface-variant);
  position: absolute;
  pointer-events: none;
  left: 0;
  top: 0;
  transition: all 0.35s cubic-bezier(0.2, 0, 0, 1);
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: inherit;
}
.textfield-filled .leading ~ label {
  margin-left: 52px;
}
.textfield-filled.open input ~ label,
.textfield-filled.open textarea ~ label {
  top: -10px;
  color: var(--md-sys-color-primary);
}
.textfield-filled input:focus ~ label,
.textfield-filled textarea:focus ~ label {
  top: -10px;
  color: var(--md-sys-color-primary);
}
.textfield-filled .placeholder {
  color: var(--md-sys-color-on-surface-variant);
  padding: 4px 16px 0 16px;
}

/* activo */
/* barra inferior */
.bar {
  position: relative;
  display: block;
  width: 100%;
}
.bar:before, .bar:after {
  content: "";
  height: 2px;
  width: 0;
  bottom: 0px;
  position: absolute;
  background: var(--md-sys-color-primary);
  transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
}
.bar:before {
  left: 50%;
}
.bar:after {
  right: 50%;
}

/* activo */
input:focus ~ .bar:before, input:focus ~ .bar:after {
  width: 50%;
}

textarea:focus ~ .bar:before, textarea:focus ~ .bar:after {
  width: 50%;
}

/* Error */
[class*=textfield-].error input,
[class*=textfield-].error textarea {
  caret-color: var(--md-sys-color-error);
}

.textfield-filled.error input,
.textfield-filled.error textarea {
  border-bottom: 1px solid var(--md-sys-color-error);
}

.textfield-outlined.error input {
  border: 1px solid var(--md-sys-color-error);
}
.textfield-outlined.error input:focus {
  outline: 1px solid var(--md-sys-color-error);
  border: 1px solid var(--md-sys-color-error);
}
.textfield-outlined.error textarea:focus {
  outline: 1px solid var(--md-sys-color-error);
  border: 1px solid var(--md-sys-color-error);
}
.textfield-outlined.error input:hover {
  outline-color: var(--md-sys-color-on-error-container);
  border: 1px solid var(--md-sys-color-on-error-container);
}
.textfield-outlined.error textarea:hover {
  outline-color: var(--md-sys-color-on-error-container);
  border: 1px solid var(--md-sys-color-on-error-container);
}
.textfield-outlined.error textarea:hover ~ label {
  color: var(--md-sys-color-on-error-container);
}
.textfield-outlined.error.open textarea:hover ~ label {
  color: var(--md-sys-color-on-error-container);
}
.textfield-outlined.error input:hover ~ label,
.textfield-outlined.error input:hover ~ .trailicon.error {
  color: var(--md-sys-color-on-error-container);
}
.textfield-outlined.error.open input:hover ~ label {
  color: var(--md-sys-color-on-error-container);
}
.textfield-outlined .trailicon.error {
  color: var(--md-sys-color-error);
  font-variation-settings: "FILL" 1, "wght" 500, "opsz" 24;
}

[class*=textfield-].error label,
[class*=textfield-].error textarea:focus ~ label, [class*=textfield-].error.open textarea ~ label,
[class*=textfield-].error input:focus ~ label, [class*=textfield-].error.open input ~ label,
[class*=textfield-].error .placeholder {
  color: var(--md-sys-color-error);
}

/* activo */
/* barra inferior */
.textfield-filled.error .bar:before, .textfield-filled.error .bar:after {
  background: var(--md-sys-color-error);
}

/* Selects */
select {
  position: relative;
}
select option {
  background-color: var(--md-sys-color-surface-variant);
  color: var(--md-sys-color-on-surface-variant);
}
select.outlined {
  background-color: var(--md-sys-color-background);
  color: var(--md-sys-color-on-surface-variant);
  outline: none;
  border: 1px solid var(--md-sys-color-outline);
  border-radius: 6px;
  width: 100%;
  white-space: pre-wrap;
  text-overflow: ellipsis;
}
select.outlined:active {
  outline: none;
}
select.outlined:focus option, select.outlined:active option {
  background-color: var(--md-sys-color-background);
}
select.outlined option {
  background-color: var(--md-sys-color-background);
  color: var(--md-sys-color-on-surface-variant);
  padding: 16px 16px;
  white-space: pre-wrap;
  width: 100%;
  text-overflow: ellipsis;
  cursor: pointer;
}
select.outlined option:hover {
  background-color: var(--md-sys-color-surface-variant);
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}
input[type=number], input[type=date] {
  -moz-appearance: textfield;
  appearance: textfield;
}

/* Firefox */
[data-theme=dark] input,
[data-theme=dark] select {
  color-scheme: dark;
}

/* ACORDEON =================================================== */
.cardaccordion {
  border-top: 1px solid var(--md-sys-color-outline);
  overflow: hidden;
}
.cardaccordion.open > .accordion-title::after {
  border-radius: 0;
}
.cardaccordion > .accordion-title::after {
  border-radius: 0 0 12px 12px;
}

.accordion {
  overflow: hidden;
}
.accordion.open > .accordion-title::before {
  transform: rotate(180deg);
}
.accordion.open > .accordion-content {
  grid-template-rows: 1fr;
}
.accordion-title {
  padding: 16px 46px 16px 24px;
  margin: 0;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}
.accordion-title::before {
  display: flex;
  height: 100%;
  position: absolute;
  padding: 0 16px;
  margin: 0;
  font-size: 1.5em;
  content: "expand_more";
  right: 0;
  bottom: 0;
  font-family: "Material Symbols Sharp";
  color: inherit;
  align-items: center;
}
.accordion-title::after {
  opacity: 0;
}
.accordion-title:hover::after {
  opacity: 0.08;
}
.accordion-title:focus::after {
  opacity: 0.12;
}
.accordion-title:active::after {
  opacity: 0.12;
}
.accordion-content {
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 0.45s cubic-bezier(0.2, 0, 0, 1);
}
.accordion-content > div {
  overflow: hidden;
  transition: padding 0.45s cubic-bezier(0.2, 0, 0, 1);
}
.accordion div:not(.open) > .accordion-content > div {
  padding-top: 0;
  padding-bottom: 0;
}

/* LISTA ====================================================== */
.list {
  padding: 8px 0;
  overflow-y: auto;
  overflow-x: hidden;
}
.list.noclick .listitem {
  cursor: default;
}
.list.noclick .listitem:not(.listitem:last-child) {
  border-bottom: 1px solid var(--md-sys-color-outline);
}
.list.border .listitem {
  border-bottom: 1px solid var(--md-sys-color-surface-variant);
}
.list.border .listitem:last-child {
  border-bottom: none;
}

.listitem {
  width: 100%;
  padding: 8px 24px 8px 16px;
  z-index: 1;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  column-gap: 16px;
  align-items: center;
  overflow: hidden;
  position: relative;
  flex-wrap: nowrap;
  min-height: 48px;
}
.listitem::before {
  background-color: var(--md-sys-color-on-surface);
  opacity: 0;
  transition: all 0.15s cubic-bezier(0, 0, 0, 1);
}
.listitem .icon {
  font-size: 24px;
  flex: 0 0 24px;
}
.listitem .icon.error {
  color: var(--md-sys-color-error);
}
.listitem div {
  overflow: hidden;
  text-overflow: ellipsis;
}
.listitem span {
  color: var(--md-sys-color-on-surface);
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}
.listitem:hover::before {
  opacity: 0.08;
}
.listitem:focus::before {
  opacity: 0.12;
}
.listitem:active::before {
  opacity: 0.12;
}

.searchresults {
  height: 100%;
}

/* DESCRIPTION GROUP ============================================ */
.block-description {
  display: flex;
  align-items: center;
  column-gap: 8px;
  padding: 8px 0;
}
.block-description div {
  flex: 1;
}

/* Pantalla de Cargando ======================================================= */
#loading-wrapper {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  flex-direction: column;
  row-gap: 30px;
  opacity: 1;
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  padding: 0 24px;
  animation: md-appear 0.35s cubic-bezier(0.05, 0.7, 0.1, 1);
  justify-content: center;
  align-items: center;
}
#loading-wrapper::before {
  background-color: var(--md-sys-color-background);
  opacity: 0.3;
}

/* CHECKBOX ==================================================== */
.listitem .checkbox {
  z-index: 1;
}

.checkbox {
  position: relative;
  width: 0;
  height: 0;
  margin: 16px;
}
.checkbox::before {
  position: absolute;
  font-family: "Material Symbols Sharp";
  font-variation-settings: "FILL" 1, "wght" 400, "GRAD" -25, "opsz" 24;
  font-size: 24px;
  line-height: 18px;
  text-align: center;
  content: "check_box_outline_blank";
  top: -9px;
  left: -12px;
  width: 18px;
  height: 18px;
  pointer-events: all;
  z-index: 2;
  color: var(--md-sys-color-on-surface);
}
.checkbox:checked::before {
  content: "check_box";
  color: var(--md-sys-color-primary);
}
.checkbox::after {
  position: absolute;
  content: "";
  top: -20px;
  left: -20px;
  background-color: var(--md-sys-color-on-surface);
  width: 40px;
  height: 40px;
  pointer-events: all;
  border-radius: 50%;
  opacity: 0;
}
.checkbox:hover::after {
  opacity: 0.08;
}
.checkbox:checked::after {
  background-color: var(--md-sys-color-primary);
}
.checkbox:focus {
  outline: none;
}
.checkbox:focus::after {
  opacity: 0.12;
}
.checkbox:active::after {
  opacity: 0.12;
}

/* SWITCH ====================================================== */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 32px;
  width: 52px;
  background-color: var(--md-sys-color-surface-variant);
  border: 2px solid var(--md-sys-color-outline);
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
}
.slider:after {
  position: absolute;
  content: "";
  height: 40px;
  width: 40px;
  left: -6px;
  top: -6px;
  background-color: var(--md-sys-color-primary);
  -webkit-transition: 0.45s cubic-bezier(0, 0, 0, 1);
  transition: 0.45s cubic-bezier(0, 0, 0, 1);
  border-radius: 50%;
  opacity: 0;
}
.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 6px;
  top: 6px;
  background-color: var(--md-sys-color-outline);
  -webkit-transition: 0.45s cubic-bezier(0, 0, 0, 1);
  transition: 0.45s cubic-bezier(0, 0, 0, 1);
  border-radius: 50%;
}
.slider:hover::before {
  background-color: var(--md-sys-color-on-surface-variant);
  outline: 4px;
}
.slider:hover::after {
  opacity: 0.08;
}
.slider:active::before {
  left: 0;
  top: 0;
  height: 28px;
  width: 28px;
}
.slider:active::after {
  opacity: 0.12;
}

input:checked + .slider {
  background-color: var(--md-sys-color-primary);
  border: 2px solid var(--md-sys-color-primary);
}
input:checked + .slider:before {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translate(16px, -4px);
  background-color: var(--md-sys-color-on-primary);
  height: 24px;
  width: 24px;
  content: "done";
  text-align: center;
  line-height: 24px;
  font-family: "Material Symbols Sharp";
}
input:checked + .slider:after {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translate(20px);
}
input:checked + .slider:hover::before {
  background-color: var(--md-sys-color-primary-container);
}
input:checked + .slider:active::before {
  left: 4px;
  top: 4px;
  height: 28px;
  width: 28px;
  line-height: 28px;
}

/* PLACEHOLDER =================================================== */
.plh-glow,
.plh-wave {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-top: 4px;
  margin-bottom: 4px;
}

.plh-glow .plh::after {
  background-color: var(--md-sys-color-on-tertiary-container);
  animation: anim-plh-glow 0.7s cubic-bezier(0, 0, 0, 1) infinite alternate;
}

.plh-wave .plh::after {
  opacity: 0.2;
  background-image: linear-gradient(0deg, var(--md-sys-color-tertiary-container), var(--md-sys-color-on-tertiary-container), var(--md-sys-color-tertiary-container));
  background-image: -moz-linear-gradient(0deg, var(--md-sys-color-tertiary-container), var(--md-sys-color-on-tertiary-container), var(--md-sys-color-tertiary-container));
  background-image: -webkit-linear-gradient(0deg, var(--md-sys-color-tertiary-container), var(--md-sys-color-on-tertiary-container), var(--md-sys-color-tertiary-container));
  animation: anim-plh-wave 1.5s cubic-bezier(0.2, 0, 0, 1) infinite linear;
}

.plh {
  background-color: var(--md-sys-color-tertiary-container);
  content: "";
  font-size: inherit;
  height: inherit;
  display: flex;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
}
.plh::before {
  content: "-";
  color: transparent;
  width: 0;
  font-size: inherit;
}

/* TOOLTIPS ====================================================== */
.tooltip {
  position: relative;
  top: 50%;
  left: 0;
}
.tooltip:before {
  content: attr(tooltip);
  position: absolute;
  bottom: -100%;
  left: 50%;
  padding: 4px 8px;
  min-height: 12px;
  transform: translateX(-50%) scale(0.8);
  opacity: 0;
  transition: all 0.25s cubic-bezier(0, 0, 0, 1);
  transform-origin: top;
  background: var(--md-sys-color-on-surface-variant);
  color: var(--md-sys-color-surface-variant);
  border-radius: 4px;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
}
.tooltip:hover:before {
  transition: all 0.25s cubic-bezier(0, 0, 0, 1);
  transition-delay: 1.5s;
  transform: translateX(-50%) scale(1);
  opacity: 1;
}

.disabled .tooltip:before {
  display: none;
}

/* SNACKBAR =============================================== */
.notification {
  position: fixed;
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  z-index: 50;
  row-gap: 8px;
  bottom: 24px;
  flex-direction: column;
  align-items: center;
}

.snackbar {
  animation: snackbarShow 0.25s cubic-bezier(0.2, 0, 0, 1);
  grid-template-rows: 1fr;
  display: grid;
  z-index: 50;
  background-color: var(--md-sys-color-inverse-surface);
  border-radius: 4px;
  min-height: 48px;
  padding: 0 8px 0 16px;
  min-width: 344px;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.14), 0 3px 4px 0 rgba(0, 0, 0, 0.12), 0 1px 7px 0 rgba(0, 0, 0, 0.2);
}
.snackbar.fade {
  animation: md-appear-backwards 0.25s cubic-bezier(0.2, 0, 0, 1);
  opacity: 0;
}
.snackbar > div {
  overflow: hidden;
  color: var(--md-sys-color-inverse-on-surface);
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  animation: snackbarcontentShow 0.3s cubic-bezier(0.2, 0, 0, 1);
}
.snackbar span {
  flex: 10;
  color: var(--md-sys-color-inverse-on-surface);
}
.snackbar .button-text {
  color: var(--md-sys-color-inverse-primary);
}

/* CHECKBOX =================================================== */
.radio {
  display: flex;
  align-items: center;
  position: relative;
  margin: 4px 0;
  font-size: 16px;
  line-height: 24px;
  width: 100%;
}

input[type=radio] {
  position: absolute;
  top: 0;
  left: 0;
  width: 36px;
  height: 20px;
  opacity: 0;
  z-index: 0;
}

.radiolabel {
  display: block;
  padding: 0 0 0 32px;
  cursor: pointer;
  color: var(--md-sys-color-on-surface);
  width: 100%;
}
.radiolabel:read-only, .radiolabel:disabled {
  cursor: default;
}
.radiolabel:before {
  content: "";
  position: absolute;
  top: 4px;
  left: 4px;
  width: 16px;
  height: 16px;
  background-color: transparent;
  outline: 2px solid var(--md-sys-color-on-surface-variant);
  border-radius: 16px;
  z-index: 1;
  transition: outline-color 0.3s cubic-bezier(0.2, 0, 0, 1);
}
.radiolabel:after {
  content: "";
  position: absolute;
  top: 8px;
  left: 8px;
  width: 8px;
  height: 8px;
  background-color: var(--md-sys-color-primary);
  border-radius: 50%;
  z-index: 2;
  transform: scale(0, 0);
  transition: transform 0.3s cubic-bezier(0.2, 0, 0, 1);
}

input[type=radio]:checked + .radiolabel:before {
  outline-color: var(--md-sys-color-primary);
}
input[type=radio]:checked + .radiolabel:after {
  transform: scale(1, 1);
}

.expandable-card.expanded {
  flex: 0 0 100%;
}

/* DISEÑO RESPONSIVO ============================================= */
/* Pantallas Medianas (md) */
@media only screen and (max-width: 1000px) {
  .searcharea.morph {
    position: fixed;
    display: flex;
    z-index: 11;
    top: 64px;
    flex-direction: column;
    background-color: var(--md-sys-color-surface-variant);
    border-radius: 0;
    margin: 0 0 16px 0;
    min-height: 64px;
    width: calc(100% - 88px);
    left: 0;
    margin-left: 88px;
    max-width: unset;
  }
  .searcharea.morph:has(> .searchsuggestions.show) {
    border-radius: 0 0 24px 24px;
    overflow: hidden;
  }
  .searcharea.morph .searchbar {
    height: 64px;
    border-radius: 0;
  }
  .searcharea.morph .searchbar + span {
    margin-top: 64px;
  }
  .searcharea.morph .searchbar .searchfield {
    outline: none;
    border: 0;
    color: var(--md-sys-color-on-surface);
    padding: 0 56px 0 64px;
    height: 100%;
  }
  .searcharea.morph .searchbar .button-trail {
    right: 8px;
    top: 8px;
    height: 48px;
    font-size: 24px;
  }
  .searcharea.morph .searchbar [class*=button-] {
    border-radius: 0;
    padding: 0 16px;
  }
  .searcharea.morph .searchbar button[type=submit] {
    position: absolute;
  }
  .searcharea.morph + [class*=card-] {
    margin-top: 64px;
  }
  .searcharea.morph .searchsuggestions {
    margin-top: 64px;
  }
  .panel #footer,
  .button-fab + #footer {
    margin-bottom: 72px;
  }
}
/* Teléfonos móviles (sm) */
@media only screen and (max-width: 700px) {
  .dialog.fullscreen [class*=button-]::after, .dialog.fullscreen #dialog-cancel::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: " ";
    pointer-events: none;
  }
  .content {
    margin-left: 0px;
    overflow: overlay;
    min-height: calc(100% - 64px - 80px);
  }
  .topbar .logo {
    justify-content: left;
  }
  .panel #footer,
  .panel page-footer {
    margin-bottom: 0;
  }
  .footer {
    margin-bottom: 60px;
  }
  #footer {
    margin-bottom: 60px;
  }
  #footer:after {
    margin-bottom: 60px;
  }
  .notification {
    bottom: 96px;
  }
  .searcharea.morph {
    width: 100%;
    left: 0;
    margin-left: 0;
  }
  .searchbar {
    padding: 0;
  }
  .dialog.fullscreen {
    border-radius: 0;
    min-width: unset;
    max-width: unset;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    box-shadow: none;
  }
  .dialog.fullscreen .icon {
    display: none;
  }
  .dialog.fullscreen .dialog-header {
    height: 56px;
    width: 65%;
    line-height: 56px;
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0 0 0 56px;
  }
  .dialog.fullscreen .dialog-header::before {
    content: "";
    position: absolute;
    height: 56px;
    width: 100%;
    top: 0;
    left: 0;
    display: block;
    border-bottom: 1px solid var(--md-sys-color-outline);
  }
  .dialog.fullscreen .dialogcontent {
    padding: 24px;
  }
  .dialog.fullscreen .buttonarea {
    position: absolute;
    top: -24px;
    left: 0;
    height: 56px;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    padding: 0;
  }
  .dialog.fullscreen [class*=button-] {
    height: 100%;
    border-radius: 0;
    align-items: center;
    padding: 0 24px;
    background-color: transparent;
    color: var(--md-sys-color-primary) !important;
  }
  .dialog.fullscreen #dialog-cancel {
    display: flex;
    height: 100%;
    border-radius: 0;
    align-items: center;
    color: transparent !important;
    width: 56px;
  }
  .dialog.fullscreen #dialog-cancel::before {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Material Symbols Sharp";
    font-size: 1.5em;
    color: var(--md-sys-color-on-surface);
    content: "clear";
  }
  .dialog.fullscreen [class*=button-]::after {
    border-radius: 0;
    align-items: center;
  }
  .dialog.fullscreen [class*=button-]:hover {
    box-shadow: none;
  }
  .dialog.fullscreen [class*=button-]:hover::after {
    background-color: var(--md-sys-color-primary);
  }
}
/* LOADING ANIMATION ==================================================================== */
.skeleton {
  animation: md-spinner 0.3s cubic-bezier(0.2, 0, 0, 1) infinite alternate;
}

.circle-badge {
  position: absolute;
  height: 52px;
  width: 52px;
  padding: 8px;
  background-color: var(--md-sys-color-primary);
  border-radius: 50%;
  top: 0;
  right: 0;
  z-index: 2;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.14), 0 3px 4px 0 rgba(0, 0, 0, 0.12), 0 1px 7px 0 rgba(0, 0, 0, 0.2);
}
.circle-badge .icon {
  color: var(--md-sys-color-on-primary);
  text-align: center;
  width: 36px;
}